<template lang="html">
  <div class="df-apprec">
    <div class="main">
      <sub-apprec
        v-for="(apprec, index) in apprecs"
        :apprec="apprec"
        :key="index"
      ></sub-apprec>
      <el-button
        class="spinner btn-default"
        v-show="spinnerVisible"
        :loading="isLoading"
      >Loading...</el-button>
      <span
        class="last-page"
        v-if="isLastPage"
      >:)</span>
    </div>
    </div>
  </div>
</template>

<script>
import SubApprec from '@/components/subApprec'
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'appreciation',
  components: {
    SubApprec
  },
  data() {
    return {
      spinnerVisible: false,  // 
      isLoading: false,         // 
      isLastPage: false // 
    }
  },
  computed: {
    ...mapGetters([
      'apprecs',
      'apprecCurrentPage',
      'apprecLastPage'
    ])
  },
  // , scroll 
  beforeRouteLeave(to, from, next) {
    window.removeEventListener('scroll', this.next, false)
    next()
  },
  async created() {
    // 
    this.resetPagination()
    // 
    await this.loadApprecList()
    //  window  scroll 
    window.addEventListener('scroll', this.next, false)
  },
  methods: {
    resetPagination() {
      this.storeApprecCurrentPage(0)
      this.storeApprecLastPage(0)
    },
    // 
    async next() {
      //     
      if (document.body.scrollTop + window.innerHeight >= document.body.scrollHeight &&
        this.apprecCurrentPage < this.apprecLastPage && !this.isLoading) {
        // 50px
        document.querySelector('.df-apprec').style.marginBottom = '50px'
        //    spinner
        this.spinnerVisible = this.isLoading = true
        //  Loading 
        await this.loadApprecList(true)
        //    spinner
        this.spinnerVisible = this.isLoading = false
        // 
        document.querySelector('.df-apprec').style.marginBottom = '0'
      } else if (this.apprecCurrentPage === this.apprecLastPage && !this.isLastPage) {
        // 
        this.$message({
          message: ':)',
          type: 'warning',
          customClass: 'c-msg'
        })
        // 50px
        document.querySelector('.df-apprec').style.marginBottom = '50px'
        this.isLastPage = true
      }
    },
    ...mapActions([
      'loadApprecList',
      'storeApprecCurrentPage',
      'storeApprecLastPage'
    ])
  }
}
</script>

<style lang="stylus" scoped>
@import '../../common/stylus/common'

.df-apprec
  fj(center,center)
  margin-top 20px
  .main
    width 50%
    position relative
    .spinner
      position absolute
      width 100%
    .last-page
      color Red
</style>



